import interfaces from '../../../utils/interfaces.js';
import publishHeader from '../../../components/home/publishHeader.vue';
export default {
	components: {
		publishHeader
	},
	data() {
		return {
			footerbottom: "0",
			filesArray: [],
			limit: 3,
			editText: '',
			showMP: ""
		}
	},
	onNavigationBarButtonTap(e) {
		// console.log(e);
		if (e.index == 1) {
			// 发布
			this.upload();
		} else if (e.index == 0) {
			// home 跳转

			uni.switchTab({
				url: "../home/home"
			})

		}
	},
	onLoad() {
		// 兼容h5的底部菜单



		
		// 兼容小程序

		this.showMP = true;

	},
	methods: {
		cancelClick(){
			uni.switchTab({
				url: "../home/home"
			})
			
		},
		insertImg() {
			if (this.filesArray.length >= this.limit) {
				uni.showToast({
					title: '已达到最大上传数量',
					icon: 'none'
				})

				return;
			}

			uni.chooseImage({
				count: 3,
				sizeType: ['original', "compressed"],
				sourceType: ['album', 'camera'],
				success: (res => {
					// console.log(res);
					this.filesArray = [...this.filesArray, ...res.tempFilePaths];
				})
			})
		},
		deleleImg(index) {
			uni.showModal({
				title: '提示',
				content: "确定要删除此项吗?",
				success: res => {
					if (res.confirm) {
						this.filesArray.splice(index, 1);
					}
				}
			})
		},
		upload() {
			// 参数
			let params = {
				title: this.editText,
				source: "米斯特吴",
				comment_count: 10,
				datetime: new Date()
			}

			// 发起请求
			this.request({
				url: interfaces.postUpload,
				method: "POST",
				data: params,
				success: res => {
					// console.log(res);
					uni.showToast({
						title: "发布成功!",
						icon: 'success'
					})

					// this.$router.push("/");
					// this.$forceUpdate();

					uni.switchTab({
						url: "../home/home"
					})
				}
			})
		}
	}
}
